export default class Browser {
  constructor(health, attackPower, name) {
  
    this.health = health;
    this.attackPower = attackPower;
    this.name = name;
    this.img = new Image
  }

  render() {

  }

  attack(opponent) { // opponent should be other instance of browser class
    const dmg = Math.floor(Math.random * this.attackPower);
    opponent.health -= dmg;
  }

  heal() {
    const healing = Math.floor(Math.random * 20) + 10;
    this.health += healing;
  }

}